<template>
  <div class="row">
    <div class="col-lg-6">
      <div class="mb-2">
        <div v-for="(user, index) in users" :key="user.name">
          <div class="mb-3">
            <div class="d-flex align-items-center">
              <img :src="user.picture.thumbnail" :alt="user.name.first" class="rounded-circle" />
              <h4 class="mb-0 ms-3">{{ user.name.first }}</h4>
            </div>
            <StarRating
              :star-size="20"
              :read-only="true"
              :rating="Math.trunc(Math.random() * (5 - 3) + 3)"
              :text-class="'text-warning'"
            />
          </div>
          <p>
            <span v-if="index === 0">
              {{ text }}
            </span>
            <span v-if="index === 1">
              {{ text2 }}
            </span>
            <span v-if="index === 2">
              {{ text3 }}
            </span>
          </p>
        </div>
      </div>
    </div>
    <div class="col-lg-6">
      <div class="review_box">
        <h4></h4>
        <StarRating
          class="mb-3"
          :star-size="20"
          :animate="true"
          :glow="1"
          :text-class="'text-warning'"
        />
        <p class="mb-2"></p>
        <form
          class="row"
          id="contactForm"
          novalidate="novalidate"
        >
          <div class="col-md-12">
            <div class="form-group mb-3">
              <input type="text" class="form-control" id="name" placeholder="" />
            </div>
          </div>
          <div class="col-md-12">
            <div class="form-group mb-3">
              <input type="email" class="form-control" id="email" placeholder="Email" />
            </div>
          </div>
          <div class="col-md-12">
            <div class="form-group mb-3">
              <input type="tel" class="form-control" id="number" placeholder="" />
            </div>
          </div>
          <div class="col-md-12">
            <div class="form-group mb-3">
              <textarea
                class="form-control"
                id="message"
                rows="3"
                placeholder=""
              ></textarea>
            </div>
          </div>
          <div class="col-md-12 text-right">
            <button
              type="button"
              class="btn btn-outline-primary"
              @click="$router.push('/products/productList')"
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import StarRating from 'vue-star-rating';

export default {
  components: {
    StarRating,
  },
  data() {
    return {
      rating: 1,
      users: [],
      reviews: [
        '',
        '',
        '!',
        ',,,,',
        '',
      ],
    };
  },
  computed: {
    text() {
      return this.reviews[Math.trunc(Math.random() * this.reviews.length)];
    },
    text2() {
      return this.reviews[Math.trunc(Math.random() * this.reviews.length)];
    },
    text3() {
      return this.reviews[Math.trunc(Math.random() * this.reviews.length)];
    },
  },
  created() {
    this.getUser();
  },
  methods: {
    getUser() {
      const url = 'https://randomuser.me/api/?results=3';
      this.$http.get(url).then((res) => {
        if (res.data.results) {
          this.users = res.data.results;
        } else {
          this.$swal({
            title: '',
            icon: 'error',
          });
          this.isLoading = false;
        }
      }).catch((err) => {
        this.$swal({
          icon: 'error',
          title: 'Oops...',
          text: 'Something went wrong!',
          footer: err,
        });
      });
    },
  },
};
</script>
